import ArticleItem from '~/components/ArticleItem.vue'
import VirtualList from '~/components/VirtualList.vue'

export default {
  components:{
    ArticleItem,
    VirtualList
  },
  data(){
    return {
      articles:[]
    }
  },
  async mounted(){
    let ret = await this.$http.get('/article')
    if(ret.code==0){
      this.articles = ret.data
    }

  }
}
